<script>
import { Pie } from 'vue-chartjs'

export default {
  name: 'BarChart',
  extends: Pie,
  props: {
    chartData: {
      type: Object,
      default: null
    },
    options: {
      type: Object,
      default: null
    }
  },
  mounted () {
    this.renderChart(this.chartData, this.options)
  },
  watch: {
    chartData: function (items) {
      this.renderChart(items, this.options)
    }
  }
}
</script>
